  margin-bottom: 20px; }

.button {
  cursor: pointer;
  border: 1px solid #d7d7d7;
  background-color: #f3f3f3;
  line-height: normal;
  padding: 10px 20px;
  text-decoration: none;
  color: #363636;
  display: inline-block;
  transition: all 0.3s; }
  .button:hover, .button:active {
    text-decoration: none; }
  .button:hover {
    background: #f9f9f9; }

.button-link {
  color: #ff473a;
  text-decoration: underline;
  border: 0;
  background: transparent;
  padding: 0; }
  .button-link:hover {
    text-decoration: none; }
  .button-link:active {
