.clear:before, .clear:after {
  content: " ";
  display: table; }

.clear:after {
  clear: both; }

.row:before, .row:after {
  content: "";
  display: table; }

.row:after {
  clear: both; }

.row {
  position: relative;
  margin-left: -15px;
  margin-right: -15px; }

.sr {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

