.banner-play {
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  border: 0;
  background: #000;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transition: .2s all; }
  .banner-play:before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -20px;
    margin-top: -40px;
    border-color: transparent transparent transparent #fff;
    transition: 100ms all ease;
    cursor: pointer;
    border-style: solid;
    border-width: 40px 0 40px 60px;
    transition: .2s all; }

.play {
