// Features

.features {
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
  @include breakpoint(m) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.feature-title {
  display: inline-block;
  position: relative;
  &:after {
    content: '';
    width: 20px;
    height: 3px;
    background: $dark-grey;
    display: block;
    margin: 10px auto;
  }
}

.feature-list {
  list-style: none;
  padding: 0;
  margin: 0;
  color: $grey;
  @include breakpoint(l) {
    padding-top: 30px;
  }
}

.feature-item {
  font-size: 14px;
  line-height: 18px;
  @include breakpoint(m) {
    width: 33%;
    float: left;
  }
  @include breakpoint (l) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.feature-item-title {
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
}

.feature-item-content {
  padding-left: 30px;
  padding-right: 30px;
  @include breakpoint(l) {
    padding-left: 40px;
    padding-right: 40px;
  }
}

.icon:before {
  content: '';
  width: 80px;
  height: 80px;
  display: block;
  margin: 20px auto;
}
.icon-strength:before {
  background: url(../img/strength.svg) 50% 50% no-repeat;
}
.icon-watch:before {
  background: url(../img/watch.svg) 50% 50% no-repeat;
}
.icon-moving:before {
  background: url(../img/moving.svg) 50% 50% no-repeat;
}
